<template>
  <div class="love_base">
    <vue-typed-js
    class="desc"
    :strings="typingTexts"
    :loop="false"
    :startDelay="300"
    :typeSpeed="100" 
    :backSpeed="50"
    :showCursor="false"
  >
    <span class="typing"></span>
  </vue-typed-js>
  <div class="love_bomm love_bomm2">
   <img src="../assets/hua.png" alt="" class="love_bomm_img">
   <div class="love_bomm_wz">
    认识乖乖的第 <span>{{days}}</span> 天  <br><br>
    今天比昨天多爱邱晶晶一点<br><br>
    比心♥
   </div>
  <div class="love_q">邱晶晶乖乖专属~</div>
  
  </div>
  
  </div>
</template>

<script>
export default {
  data() {
    return {
      typingTexts: [
        "乖乖~<br/>我是一个钢铁直男,<br/>有时候和二傻子一样，老是做错事。<br/>每次都是我惹你生气。<br/>但是我是真的喜欢你，<br/>在你身边真的舒服，很安心！<br/>乖乖，",
        "乖乖~<br/>我是一个钢铁直男,<br/>有时候和二傻子一样，老是做错事。<br/>每次都是我惹你生气。<br/>但是我是真的喜欢你，<br/>在你身边真的舒服，很安心！<br/>爱你！",
        "乖乖~<br/>我是一个钢铁直男,<br/>有时候和二傻子一样，老是做错事。<br/>每次都是我惹你生气。<br/>但是我是真的喜欢你，<br/>在你身边真的舒服，很安心！<br/>你可别丢了，",
        "乖乖~<br/>我是一个钢铁直男,<br/>有时候和二傻子一样，老是做错事。<br/>每次都是我惹你生气。<br/>但是我是真的喜欢你，<br/>在你身边真的舒服，很安心！<br/>我还差你个告白呢！",
        "乖乖~<br/>我是一个钢铁直男,<br/>有时候和二傻子一样，老是做错事。<br/>每次都是我惹你生气。<br/>但是我是真的喜欢你，<br/>在你身边真的舒服，很安心！<br/><h2>Believe me &nbsp;<span class='active'>I&nbsp;LOVE&nbsp;YOU&nbsp;!&nbsp;</span> </h2>"
      ],
      days:0
    };
  },
  created(){
    var newData = Date.parse(new Date()); // 获取当前时间，也是 毫秒级别的
    var olddata=new Date(2022+'-'+11+'-'+16+' '+11+':'+35+':'+33).getTime() 
    var dateDiff=newData-olddata
    var day= Math.floor(dateDiff / (24 * 3600 * 1000))
    // console.log(newData)
    // console.log(olddata)
    // console.log(day)
    this.days=day
  },
  mounted(){
   //获取屏幕宽高
        var windowWidth = window.screen.width;
        var windowHeight = window.screen.height;
 
        //创建雪花
        function createSnow(){
            var left = 0.1;
            var top = 0.1;
 
            //定义一个初始化随机数,使雪花在屏幕中
            var left_random = Math.random() * windowWidth;
            var top_random = Math.random()* windowHeight;
            var div = document.createElement('div');
            div.className = 'snow';
            div.style.transform = 'scale('+(Math.random())+')'
 
            document.body.appendChild(div);
 
            //雪花飘落
            setInterval(function () {
 
                div.style.left = left_random + left +'px';
                div.style.top = top_random + top +'px'
                left += 0.1;
                top += 0.1;
 
                //如果雪花跑到屏幕外面了,让雪花重新返回屏幕顶部
                if(left_random + left > windowWidth){
                    left_random = Math.random();
                    left = 0.1;
                }
 
                if(top_random + top > windowHeight){
                    top_random = Math.random();
                    top = 0;
                }
            },5)
 
        }
        for(var i = 0 ; i <50 ; i++){
            createSnow()
        }


  }
};
</script>

<style>
.desc {
  width: 98%;
  margin: auto;
  padding-top: 20px;
  text-align: left;
  height: 180px;
}
h2{
  color: #fff;
}
.love_base{
width: 100vw;
height: 100vh;  
background: rgb(239, 158, 197);
overflow: hidden;
}
 .snow{
            background: #fff;
            position: absolute;
            width: 10px;
            height: 10px;
            border-radius: 50%;
        }

.love_bomm{
  width: 100%;
  height: calc(100vh - 200px);
  position: relative;
 animation:fadenum 16s 1;
 /* animation:fadenum 1s 1; */

}
@keyframes fadenum{
   0%{opacity: 0;}
   50%{opacity: 0;}
   100%{opacity: 1;}
}


.love_bomm_img{
  width: 90%;
  margin: auto;
  height: 400px;
  position: absolute;
  left: 0px;
  top: 0px;
}
.love_bomm_wz{
  width: 90%;
  margin: auto;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  position: absolute;
  left: 0px;
  top: 127px;
  
}
.love_bomm_wz span{
  color: red;
  font-size: 20px;
}
.love_q{
  width: 88%;
  margin: auto;
  text-align: right;
  position: absolute;
  bottom: 150px;
  left: 0px;
  color: #fff;
}
.active{
  color: red;
}
</style>



